import React, { useState } from 'react';
import { NavbarBase, Item } from './styled';
const Component = ({ items, valueFilter, setValueFilter }) => {

    const renderItems = items.map((item, index) => <Item key={`${index}-item`} isSelect={item.value == valueFilter} onClick={() => setValueFilter(item.value)}>{item.name}</Item>)

    return (
        <NavbarBase>
            {renderItems}
        </NavbarBase>
    );
}

export default Component;
export { Component as NavbarBase }